import * as React from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import Layout from '../components/layout';

const IndexPage = () => {
  return (
    <Layout topPage={true}>
      <p>この Webサイトは、GatsbyJSのチュートリアルにそって制作しています。 </p>
      <StaticImage
        alt="サンプル"
        src="../img/sample.png"
        placeholder="Blurred"
      />
    </Layout>
  )
};

export default IndexPage;
